body {
  line-height: 22px;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

.appBar {
  div, .MuiIconButton-label {
    color: white;
  }
}

.app {
  width: 1000px;
  margin: 80px auto 0;
}

footer {
  margin-top: 20px;
}

div, span, select, option, p, body, label, input {
  font-size: 13px;
}

select, option, input {
  font-size: 12px;
  font-family: "Open Sans", sans-serif;
}

a, .link {
  text-decoration: none;

  &:hover {
    border-bottom: 1px solid #000000;
  }
}
